.header__wrapper{
  display: flex;
  justify-content: space-between;
  padding: 59px 0;
  
}

.burger__menu{
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  cursor: pointer;
  
  .burger__line{
    display: block;
    width: 34px;
    height: 3px;
    background-color: #000;
    margin-bottom: 8px;


    &:last-child{
      width: 18px;
    }
  }
}
