html {
  box-sizing: border-box;
  font-size: 10px;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  background-color: #F4F4F4;
  margin: 0;
  font-family: 'Fira Sans', sans-serif;

  &.no-scroll {
    overflow: hidden;
  }
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}


.active{
  color: #1434E1;
  display: flex !important;
  opacity: 1 !important;
  
}


li {
  font-weight: normal;
  font-size: 18px;
  line-height: 28px;
  list-style: none;
  opacity: .7;
  margin-bottom: 2.1rem;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important; // stylelint-disable-line declaration-no-important
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1390px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}
.button{
  max-width: 19rem;
  padding: 17px 24px;
  color: #fff;
  background-color: #3726F2;
  font-weight: 500;
  font-size: 1.4rem;
  text-align: center;
  letter-spacing: 4px;
  cursor: pointer;
  text-transform: uppercase;
  transition: all .2s ease-in-out;
  &--works{
    max-width: 23rem;
    line-height: 1;
    padding:23px 24px 22px 24px;
  }

  &:hover {
    background-color: darken(#3726F2, 100%);
  }
}
.section-title{
  font-weight: 600;
  font-size: 4rem;
  line-height: 5rem;
  letter-spacing: -1px;
  &--services{
    max-width: 54rem;
  }
  &--testimonials{
    padding: 8rem 0 12rem 0;
  }
}
.section-name{
  font-weight: 500;
  font-size: 1.4rem;
  line-height: 24px;
  letter-spacing: 4px;
  text-transform: uppercase;
  color: #222222;
  opacity: 0.7;
  position: relative;
 
  padding-left: 3rem;

  &::before {
    position: absolute;
    content: "";
    display: block;
    top: 50%; 
    transform: translateY(-50%);
    left:0;
    height: 2px;
    width: 15px;
    background-color: #8F8F8F;
  }

  &--clients{
    opacity: 1;
  }
}
.section__right-description{
  font-weight: normal;
  font-size: 1.8rem;
  line-height: 2.8rem;
  opacity: .7;
  margin:3rem 0;

}

.section-author{
  font-weight: 500;
  font-size: 2.2rem;
  line-height: 3.2rem; 


  &--features{
    letter-spacing: 4px;
    margin-left: 3rem;
    text-transform: uppercase;
  }
}
.arrows{
  position: absolute;
  bottom: -10rem;
  background-color: #C4C4C4;
  border:none;
  outline: none;
  padding:1.8rem 2.3rem;
  font-size: 2rem;
  color: #fff;
  z-index: 100;
  cursor: pointer;
  transition: all .2s ease-in-out;
  &--testimonials{
    bottom: 20rem;
  }
  
  &:hover {
    background-color: darken(#C4C4C4, 20%);
  }
}
.arrow__right{
  left: 8rem;
}
